// 添加请求拦截器
axios.interceptors.request.use(function (config) {
	//打开loading 遮罩层
	store.dispatch('showLoading');
	return config;
}, function (error) {
	return Promise.reject(error);
});
// 添加响应拦截器
axios.interceptors.response.use(function (response) {
	// 关闭loading 遮罩层
	store.dispatch('hideLoading');
	return response;
}, function (error) {
	return Promise.reject(error);
});





