//封装Get、Post请求格式
function apiGet (url, query) {
	var str = Object.keys(query).map(function (key) {
		return encodeURIComponent(key) + '=' + encodeURIComponent(query[key])
	}).join('&')
	//判断终端
	if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {

		let params = '{page: 1, limit: 5}'
		var result = OC.request(params);
		console.log('result -> ',result);
		str = result;
		// str = str+'&sign='+result.sign+'&time='+result.time;

	} else if (/(Android)/i.test(navigator.userAgent)) {

	};
	console.log('发起 get 请求 -> ', url + '?' + str)
	return axios.post(url, str)
}

function apiPost (url, data) {

  var resultObj = getObj(data);
	// var result = OC.request('page,limit','1,5');

	// let resultObj = JSON.parse(result);
	console.log('result ->', resultObj);
	return axios.post(url, resultObj)
}



var getObj = function(params){
    var str = "";
    var time = "";
    $.ajax({
        url:'http://lixin.xapi.wangtiansoft.com/system/getTime',
        type:'GET', //GET
        async:false,    //或false,是否异步
        timeout:5000,    //超时时间
        dataType:'json',    //返回的数据格式：json/xml/html/script/jsonp/text
        success:function(data,textStatus,jqXHR){
            time = data.data.time;
        }
    });
    params.time = time;
    let p = Object.keys(params).sort();
    for (var k in p){
        str += (p[k]+params[p[k]]);
	}
    str += "CtUyV$8MGoK8u5L*P0Q50T/b8S9iclS*LQqo";
	// alert(str);
    var sign = md5(str);
    // alert(sign);
    params.sign = sign;
    return params;
}

axios.defaults.transformRequest = [function(data) {
	console.log('data ->', data);
	let ret = Object.keys(data).map(function(key) {
		return encodeURIComponent(key) + '=' + encodeURIComponent(data[key])
	}).join('&')
	console.log('data 1 ->', ret);
	return ret
}]



